// src/components/dashboard/UpcomingBills.jsx
import { Card } from "@/components/ui/card";

const bills = [
  {
    id: 1,
    date: "May 15",
    title: "Figma - Monthly",
    amount: 150,
    sub: "Last Charge - 14 May, 2022",
  },
  {
    id: 2,
    date: "Jun 16",
    title: "Adobe - Yearly",
    amount: 559,
    sub: "Last Charge - 17 Jun, 2023",
  },
];

export default function UpcomingBills() {
  return (
    <div className="w-full">
      {/* Heading (Card ke bahar) */}
      <div className="flex justify-between items-center mb-3 flex-wrap">
        <h2 className="text-22px font-inter text-gray-500 mb-2">
          Upcoming Bill
        </h2>
        <button className="text-sm text-teal-600 font-medium hover:underline">
          View All
        </button>
      </div>

      {/* Card - fully responsive */}
      <Card
  className="
    p-4 rounded-lg shadow-sm bg-white 
    w-full h-auto
    sm:max-w-[360px] md:max-w-[380px] lg:max-w-[335px]
    min-h-[235px] mt-[-12px]
  "
>

        <div className="space-y-3">
          {bills.map((bill) => (
            <div
              key={bill.id}
              className="
                flex flex-col sm:flex-row sm:justify-between sm:items-center
                border rounded-lg px-3 py-2 
                hover:shadow transition
              "
            >
              {/* Left Info */}
              <div className="flex flex-col min-w-0">
                <span className="text-sm font-medium">{bill.date}</span>
                <span className="text-gray-600 text-sm truncate">
                  {bill.title}
                </span>
                <span className="text-xs text-gray-400 truncate">{bill.sub}</span>
              </div>

              {/* Amount */}
              <span className="font-semibold text-gray-800 mt-2 sm:mt-0 shrink-0">
                ${bill.amount}
              </span>
            </div>
          ))}
        </div>
      </Card>
    </div>
  );
}
