"use client";
import { Card, CardContent } from "@/components/ui/card";
import { ChevronLeft, ChevronRight } from "lucide-react";
import { useState } from "react";
import { useRouter } from "next/navigation"; // 👈 import router

const accounts = [
  {
    type: "Credit Card",
    number: "**** **** **** 2598",
    balance: "$25,000",
    color: "bg-teal-600",
  },
  {
    type: "Savings",
    number: "**** **** **** 7733",
    balance: "$5,000",
    color: "bg-blue-600",
  },
  {
    type: "Wallet",
    number: "UPI - xxxxxx@ybl",
    balance: "$1,200",
    color: "bg-orange-500",
  },
];

export default function TotalBalance() {
  const [index, setIndex] = useState(0);
  const router = useRouter();

  const prev = () => setIndex((index - 1 + accounts.length) % accounts.length);
  const next = () => setIndex((index + 1) % accounts.length);

  return (
    <div className="w-full max-w-xs ml-0">
      {/* Heading */}
      <h2 className="text-22px font-inter text-gray-500 mb-2">
        Total Balance
      </h2>

      <Card
        className="p-2 sm:p-3 rounded-lg bg-white shadow-sm w-full cursor-pointer"
        onClick={() => router.push("/accounts")} // 👈 click karne par redirect
      >
        {/* ✅ Total Balance Amount (dynamic banaya) */}
        <p className="text-lg font-bold text-gray-900 mb-2">
          {accounts[index].balance}
        </p>

        <CardContent className="p-0">
          {/* Active Account */}
          <div
            className={`rounded-md p-3 text-white transition-all ${accounts[index].color}`}
          >
            <p className="text-[10px]">Account Type</p>
            <h3 className="text-sm font-semibold">{accounts[index].type}</h3>
            <p className="text-[10px] mt-0.5">{accounts[index].number}</p>
            <div className="mt-2 flex items-center justify-between">
              <span className="text-base font-bold">
                {accounts[index].balance}
              </span>
              <span className="text-sm">💳</span>
            </div>
          </div>
        </CardContent>

        {/* Controls + Dots */}
        <div className="mt-2 flex items-center justify-between">
          <button
            onClick={(e) => {
              e.stopPropagation();
              prev();
            }}
            className="flex items-center gap-1 text-xs text-gray-600 hover:text-gray-900"
          >
            <ChevronLeft className="w-3 h-3" />
            Prev
          </button>

          <div className="flex gap-1">
            {accounts.map((_, i) => (
              <span
                key={i}
                className={`h-1.5 w-1.5 rounded-full ${
                  i === index ? "bg-gray-800" : "bg-gray-300"
                }`}
              />
            ))}
          </div>

          <button
            onClick={(e) => {
              e.stopPropagation();
              next();
            }}
            className="flex items-center gap-1 text-xs text-gray-600 hover:text-gray-900"
          >
            Next
            <ChevronRight className="w-3 h-3" />
          </button>
        </div>
      </Card>
    </div>
  );
}
